<script>
export default {
  data() {
    return {
      count: 0
    }
  }
}
</script>
<template>
  <button class="btn" @click="count++">You clicked me {{ count }} times.</button>
</template>

<style>
.btn {
  height: 100px;
  border-radius: 10px;
  background-color: #00ff40;
}
</style>


